.App {
  text-align: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* @import "https://www.w3schools.com/w3css/4/w3.css"; */

Paper.landing-page-surface {
  border-radius: 20px;
}
#login,
#signup {
  animation: content-slide-in 1s forwards;
}
@keyframes content-slide-in {
  from {
    transform: translateX(-15%);
  } to {
    transform: translateX(0%);
  }
}

@media screen and (min-width: 900px) {
  .landing-page-surface {
    width: calc(100% - 96px);
    max-width: 900px;
    min-height: 500px;
    margin: calc((100vh - 600px) / 2) auto;
    padding: 24px;
  }
}
@media screen and (max-width: 899px) {
  .landing-page-surface {
    width: 488px;
    margin: calc((100vh - 784px) / 2) auto;
    padding: 6px;
  }
}
@media screen and (max-width: 500px) {
  .landing-page-surface {
    width: calc(100% - 24px);
    margin: calc((100vh - 683px) / 2) auto;
    padding: 6px;
  }
}

@media screen and (min-width: 900px) {
  #todolist-pic {
    margin: 62px 0;
    min-width: 378px;
    max-width: 378px;
    min-height: 252px;
    max-height: 252px;
  }
}
@media screen and (max-width: 899px) {
  #todolist-pic {
    max-width: 476px;
  }
}
@media screen and (max-width: 500px) {
  #todolist-pic {
    max-width: calc(100vw - 36px);
  }
}

#sign-in {
  margin: 32px 0;
}

#sign-in-error {
  margin: 32px 0 0;
}

#sign-in-error-message {
  color: #d32f2f;
  line-height: 20px;
  font-size: 12px;
  text-align: left;
  margin: 8px 0 4px 100px;
}

.landing-page {
  line-height: 40px;
  margin-bottom: 32px;
}

.landing-page-error {
  line-height: 40px;
}

.MuiFormHelperText-root.Mui-error {
  line-height: 12px;
  margin-left: 0px;
  margin-bottom: 16px;
}

.login {
  width: 250px;
}

Button.landing-page {
  line-height: 40px;
  width: 250px;
  margin-bottom: 24px;
  padding: 0;
}

#landing-image {
  min-height: 336px;
  background-image: url('images/todolist.jpg');
}

.todos-surface {
  padding: 12px;
  min-width: 80%;
  max-width: 80%;
  margin: calc((100vh - 840px)/2) auto;
}

@media screen and (max-width: 600px) {
  .todos-surface {
    min-width: calc(100% - 48px);
    margin: 12px;
  }
}

div.todo-card-left,
div.todo-card-right
 {
  overflow: scroll;
  font-size: 20px;
  text-align: left;
  min-height: 350px;
  max-height: 350px;
}

@media screen and (max-width: 600px) {
  div.todo-card-left,
  div.todo-card-right {
    margin: 12px;
  }
}

.todo-card-left {
  margin: 12px 6px 12px 12px;
}

.todo-card-right {
  margin: 12px 12px 12px 6px;
}

.green {
  color: green;
}

span.MuiCardHeader-title {
  font-size: 24px;
}

.todo-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todo {
  line-height: 42px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin: 0 12px;
}

#todo-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  background-color: white;
  padding: 20px;
  border: 1px solid black;
}

@media only screen and (min-width: 600px) {
  #todo-modal {
    width: 500px;
  }
};

#todo-form-header {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 18px;
  border-bottom: 1px solid #D3D3D3;
}

.todo-form {
  display: flex;
  flex-wrap: wrap;
  line-height: 40px;
  margin-bottom: 18px;
  font-weight: 400;
}

.todo-form-title {
  width: 32%;
}

.todo-form > div > div {
  min-width: 200px;
}

@media only screen and (min-width: 600px) {
  .todo-form > div > div {
    width: 325px;
  }
}

.todo-data {
  text-align: left;
}

.todo-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todo-header > h2 {
  margin: 0;
}

#delete {
  margin-left: 12px;
}

.subtitle {
  font-weight: 600;
}

#todo-category {
  display: flex;
  line-height: 24px;
}

#todo-category, 
#todo-description {
  background-color: #C5FAD5;
  padding: 6px;
}

.todo-header,
#todo-title, 
#todo-dates {
  background-color: #FFFFD2;
  padding: 6px;
}

#todo-icon {
  margin-right: 6px;
  max-height: 24px;
}

.todo-surface {
  padding: 12px;
  min-width: calc(80% - 24px);
  max-width: 80%;
  margin: 24px auto 0 auto;
  @media screen and (max-width: 600px) {
    min-width: calc(100% - 48px);
    /* max-width: 100%; */
    margin: 12px;
  }
}

#delete-todo {
  color: #d0342c;
}

#warning {
  font-size: 48px;
}

#delete-text {
  color: #d0342c;
}

@media screen and (min-width: 601px) {
  #delete-text {
    display: grid;
    grid-template-columns: 10% 90%;
  }
}


div#calendar-surface {
  background-color: #FFD839;
  padding: 12px;
  min-height: calc(100vh - 300.5px);
  /* max-height: calc(100vh - 300.5px); */
  min-width: calc(80% - 24px);
  max-width: 80%;
  margin: 12px auto 24px auto;
}

@media screen and (max-width: 600px) {
  div#calendar-surface {
    min-width: calc(100% - 48px);
    margin: 12px;
  }
}

@media screen and (max-width: 600px) {
  #delete-text {
    display: grid;
    grid-template-columns: 15% 85%;
  }
  #calendar {
    min-height: 450px;
    max-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  #delete-text {
    display: grid;
    grid-template-columns: 20% 80%;
  }
}

#calendar {
  max-height: calc(100vh - 369px);
  max-width: 100%;
}
